import { TEvent } from "@/lib/types";
import { useAuth } from "@/context/AuthContext";

export function EventCard({ event }: { event: TEvent }) {
  const { isAuthenticated } = useAuth();
  const isPrivate = event.permission === "private";

  if (isPrivate && !isAuthenticated) return null;

  return (
    <div className="relative bg-white border-4 border-black rounded-lg p-5 shadow-[8px_8px_0px_0px_rgba(0,0,0,1)] hover:-translate-y-1 transition-transform">
      
      {/* Header Section */}
      <div className="border-b-2 border-black border-dashed pb-3 mb-3">
        <div className="flex justify-between items-start gap-2">
           {/* Title */}
          <h3 className="text-2xl font-bold leading-none">{event.name}</h3>
          
           {/* Snoopy/Woodstock Icon based on type (Optional emoji fallback) */}
           <div className="text-3xl shrink-0">
             {event.event_type === 'workshop' ? 'üõ†Ô∏è' : event.event_type === 'activity' ? 'üéæ' : 'üé§'}
           </div>
        </div>
        
        {/* Time Pill */}
        <div className="mt-2 inline-block bg-white border-2 border-black rounded-full px-3 py-0.5 text-sm font-bold">
          {new Date(event.start_time).toLocaleTimeString([], { hour: 'numeric', minute: '2-digit' })} 
          - 
          {new Date(event.end_time).toLocaleTimeString([], { hour: 'numeric', minute: '2-digit' })}
        </div>
      </div>

      {/* Description */}
      {event.description && (
        <p className="text-lg leading-snug mb-6">
          {event.description}
        </p>
      )}

      {/* Footer Tags & Buttons */}
      <div className="flex flex-wrap items-center justify-between gap-3 mt-auto">
        {/* Type Tag (Charlie Brown Yellow) */}
        <span className={`px-3 py-1 rounded-md border-2 border-black text-sm font-bold uppercase
          ${event.event_type === 'workshop' ? 'bg-[#FACC15]' : 
            event.event_type === 'activity' ? 'bg-[#4ADE80]' : 
            'bg-[#60A5FA]'}`}>
          {event.event_type.replace("_", " ")}
        </span>

        {/* Buttons */}
        <div className="flex gap-2">
          {event.public_url && (
            <a href={event.public_url} target="_blank" className="text-base font-bold underline decoration-2 hover:text-blue-600">
              Public Link ‚Üí
            </a>
          )}
          
          {isAuthenticated && event.private_url && (
            <a href={event.private_url} target="_blank" className="bg-black text-white px-3 py-1 rounded border-2 border-transparent hover:bg-white hover:text-black hover:border-black transition-colors font-bold text-base">
              Hacker Link
            </a>
          )}
        </div>
      </div>
    </div>
  );
}