"use client";
import { TEventType } from "@/lib/types";

interface FilterBarProps {
  search: string;
  setSearch: (value: string) => void;
  typeFilter: TEventType | "all";
  setTypeFilter: (value: TEventType | "all") => void;
}

export function FilterBar({ search, setSearch, typeFilter, setTypeFilter }: FilterBarProps) {
  return (
    <div className="flex flex-col md:flex-row gap-4 mb-8 bg-white p-4 rounded-lg shadow-sm border">
      {/* Search Input */}
      <div className="flex-1">
        <label className="text-xs font-semibold text-gray-500 uppercase tracking-wide mb-1 block">
          Search
        </label>
        <input 
          className="w-full border border-gray-300 p-2 rounded-md focus:ring-2 focus:ring-blue-500 outline-none transition" 
          placeholder="Search events by name..." 
          value={search}
          onChange={(e) => setSearch(e.target.value)}
        />
      </div>

      {/* Type Dropdown */}
      <div className="w-full md:w-48">
        <label className="text-xs font-semibold text-gray-500 uppercase tracking-wide mb-1 block">
          Event Type
        </label>
        <select 
          className="w-full border border-gray-300 p-2 rounded-md bg-white focus:ring-2 focus:ring-blue-500 outline-none transition"
          value={typeFilter}
          onChange={(e) => setTypeFilter(e.target.value as any)}
        >
          <option value="all">All Events</option>
          <option value="workshop">Workshop</option>
          <option value="activity">Activity</option>
          <option value="tech_talk">Tech Talk</option>
        </select>
      </div>
    </div>
  );
}