"use client";
import { useAuth } from "@/context/AuthContext";
import { useState } from "react";
import { LoginModal } from "./LoginModal";

export function Navbar() {
  const { isAuthenticated, logout } = useAuth();
  const [showModal, setShowModal] = useState(false);

  return (
    <nav className="relative z-50 pt-8 pb-4 px-4 flex flex-col md:flex-row items-center justify-between max-w-6xl mx-auto">
      
      {/* 1. The Logo Section (Snoopy on House) */}
      <div className="flex items-end gap-4">
        {/* Make sure you save your image as /public/snoopy-logo.png */}
        <div className="relative w-24 h-24 -mb-4 shrink-0">
          <img 
            src="/snoopy-logo.png" 
            alt="Snoopy on Doghouse" 
            className="w-full h-full object-contain drop-shadow-[4px_4px_0px_rgba(0,0,0,1)]"
          />
        </div>
        <h1 className="text-4xl font-bold tracking-wide -rotate-2 mb-2">
          Hackathon Global<span className="text-sm align-top">TM</span>
        </h1>
      </div>

      {/* 2. The Login Button (Red like the doghouse) */}
      <div className="mt-4 md:mt-0">
        {isAuthenticated ? (
          <button onClick={logout} className="text-xl font-bold hover:underline decoration-wavy decoration-2">
            Good Grief (Logout)
          </button>
        ) : (
          <button 
            onClick={() => setShowModal(true)} 
            className="bg-[#E11D48] text-white text-xl px-6 py-2 rounded-xl border-2 border-black shadow-[4px_4px_0px_0px_rgba(0,0,0,1)] hover:translate-y-1 hover:shadow-none transition-all"
          >
            Hacker Login
          </button>
        )}
      </div>
      {showModal && <LoginModal onClose={() => setShowModal(false)} />}
    </nav>
  );
}