"use client";
import { useState } from "react";
import { useAuth } from "@/context/AuthContext";

export function LoginModal({ onClose }: { onClose: () => void }) {
  const { login } = useAuth();
  const [user, setUser] = useState("");
  const [pass, setPass] = useState("");
  const [error, setError] = useState("");

  const handleLogin = (e: React.FormEvent) => {
    e.preventDefault();
    if (user === "hacker" && pass === "htn2026") {
      login();
      onClose();
    } else {
      setError("Invalid credentials (try hacker / htn2026)");
    }
  };

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <form onSubmit={handleLogin} className="bg-white p-6 rounded-lg shadow-lg w-80">
        <h2 className="text-xl font-bold mb-4">Hacker Login</h2>
        <input 
          className="w-full border p-2 mb-2 rounded" 
          placeholder="Username" 
          value={user} 
          onChange={(e) => setUser(e.target.value)} 
        />
        <input 
          className="w-full border p-2 mb-2 rounded" 
          type="password" 
          placeholder="Password" 
          value={pass} 
          onChange={(e) => setPass(e.target.value)} 
        />
        {error && <p className="text-red-500 text-sm mb-2">{error}</p>}
        <div className="flex gap-2 justify-end">
          <button type="button" onClick={onClose} className="text-gray-500">Cancel</button>
          <button type="submit" className="bg-blue-600 text-white px-4 py-2 rounded">Login</button>
        </div>
      </form>
    </div>
  );
}