"use client";
import { useEffect, useState } from "react";
import { getEvents } from "@/lib/api";
import { TEvent } from "@/lib/types";
import { Navbar } from "@/components/Navbar";
import { FilterBar } from "@/components/FilterBar"; // Import this
import { EventGrid } from "@/components/EventGrid"; // Import this
import { useEventSort } from "@/hooks/useEventSort";

export default function Home() {
  const [events, setEvents] = useState<TEvent[]>([]);
  const [loading, setLoading] = useState(true);

  // Custom Hook for Logic
  const { search, setSearch, typeFilter, setTypeFilter, filteredEvents } =
    useEventSort(events);

  useEffect(() => {
    getEvents()
      .then((data) => {
        setEvents(data);
        setLoading(false);
      })
      .catch((err) => console.error(err));
  }, []);

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="animate-pulse text-blue-600 font-medium">
          Loading Hackathon Global...
        </div>
      </div>
    );
  }

  return (
    <main className="min-h-screen bg-gray-50 pb-20">
      <Navbar />

      <div className="container mx-auto px-4 mt-8 max-w-6xl">
        <div className="mb-6">
          <p className="text-gray-500">Welcome to Hackathon Global 2026.</p>
          <h2 className="text-3xl font-bold text-gray-900">Event Schedule</h2>
        </div>

        {/* The Clean Components */}
        <FilterBar
          search={search}
          setSearch={setSearch}
          typeFilter={typeFilter}
          setTypeFilter={setTypeFilter}
        />

        <EventGrid events={filteredEvents} />
      </div>
    </main>
  );
}
